import React, { useEffect } from "react";
import { Field, Form, Formik } from "formik";
import { useParams } from "react-router-dom";
import { useStudentContext } from "../context/StudentContext";

function EditStudent() {
  let { id } = useParams();
  let { getSingleStudentDetails, editInitValue, updateStudentDetails } =
    useStudentContext();

  let initialValues = { ...editInitValue };

  useEffect(() => {
    getSingleStudentDetails(id);
  }, []);

  let onSubmit = (values, onSubmitProps) => {
    updateStudentDetails(values);
    //onSubmitProps.resetForm();
  };

  return (
    <>
      <div className="row justify-content-center">
        <div className="col-8 col-lg-4">
          <p className="text-center mt-4 h3 text-primary">
            <span className="fa fa-edit"></span> Edit Student
          </p>
          <Formik
            initialValues={initialValues}
            onSubmit={onSubmit}
            enableReinitialize
          >
            <Form className="card px-5 py-3">
              <div className="mb-3">
                <label htmlFor="" className="form-label">
                  Student Name
                </label>
                <Field
                  type="text"
                  name="name"
                  className="form-control"
                  autoComplete="off"
                />
              </div>
              <div className="mb-3">
                <label htmlFor="" className="form-label">
                  Course
                </label>
                <Field
                  as="select"
                  name="course"
                  type="text"
                  className="form-select"
                >
                  <option value="">---select course ---</option>
                  <option value="React">React</option>
                  <option value="Full Stack">Full Stack</option>
                  <option value="Front End">Front End</option>
                  <option value="Node JS">Node JS</option>
                </Field>
              </div>
              <button className="btn btn-success" type="submit">
                <span className="fa fa-save me-2"></span>
                Submit
              </button>
            </Form>
          </Formik>
        </div>
      </div>
    </>
  );
}

export default EditStudent;
