import { Post, BASE_URL } from "./index";
import jwt_decode from "jwt-decode";

export const postUserLogin = async (values) => {
  try {
    let url = BASE_URL + "check-login";
    let result = await Post(url, values);
    return result;
  } catch (error) {
    console.log("error", error);
  }
};

export const setToken = (token) => {
  localStorage.setItem("auth_token", token);
};

export const getToken = () => {
  return localStorage.getItem("auth_token");
};

export const resetToken = () => {
  localStorage.removeItem("auth_token");
};

export const checkToken = () => {
  let token = getToken();
  return token === null ? false : true;
};

export const getUserDataFromToken = () => {
  try {
    return jwt_decode(getToken());
  } catch (error) {
    return false;
  }
};

export const checkTokenForAccess = (navigate, callback) => {
  let isValid = checkToken();
  if (isValid) {
    callback();
  } else {
    alert("Something went wrong ... login again ");
    navigate("/");
  }
};
