import { useContext, createContext, useState } from "react";
import {
  checkToken,
  getUserDataFromToken,
  postUserLogin,
  resetToken,
  setToken,
} from "./../service/AuthService";
import { useNavigate } from "react-router-dom";

const AuthContext = createContext({});

export function ProviderAuthContext({ children }) {
  let [isLogin, setLogin] = useState(checkToken());
  let [userInfo, setUserInfo] = useState(false);
  let navigator = useNavigate();

  let makeLogin = async (value) => {
    let result = await postUserLogin(value);
    let { data, headers } = result;

    if (data.status === true) {
      alert("login successfully");
      setToken(headers.x_auth_token);
      window.location.assign("/");
    } else {
      alert(data.message);
      resetToken();
      setLogin(false);
    }
  };

  let validateUserRoute = () => {
    setLogin(checkToken());
  };

  let getUserDetails = () => {
    let userData = getUserDataFromToken();
    setUserInfo(userData);
  };

  let logout = () => {
    resetToken();
    window.location.reload();
  };

  let values = {
    makeLogin,
    isLogin,
    validateUserRoute,
    getUserDetails,
    userInfo,
    logout,
  };
  return <AuthContext.Provider value={values}>{children}</AuthContext.Provider>;
}

export function useAuthContext() {
  return useContext(AuthContext);
}
