import React, { useEffect } from "react";
import { useStudentContext } from "../context/StudentContext";
import { useNavigate } from "react-router-dom";

function StudentList() {
  let { studentList, removeStudent, getStudentList, initRequest } =
    useStudentContext();
  let navigate = useNavigate();

  useEffect(() => {
    if (initRequest === false) getStudentList();
  }, []);

  return (
    <>
      <div className="row justify-content-center">
        <div className="col-11">
          <table className="table table-bordered mt-4">
            <thead>
              <tr>
                <th width="10%">Sr No</th>
                <th width="40%">Student Name</th>
                <th width="30%">Course</th>
                <th width="20%">Action</th>
              </tr>
            </thead>
            <tbody>
              {studentList.length === 0 ? (
                <tr>
                  <td colSpan="4">
                    <p className="text-center text-danger">
                      No Student Available
                    </p>
                  </td>
                </tr>
              ) : (
                studentList.map((student, index) => (
                  <tr key={index}>
                    <td>{index + 1}</td>
                    <td>{student.name}</td>
                    <td>{student.course}</td>
                    <td>
                      <button
                        onClick={() => removeStudent(student._id, index)}
                        className="fa fa-trash btn btn-sm 
                    btn-danger"
                      ></button>
                      <button
                        className="btn btn-primary btn-sm fa fa-edit ms-1"
                        onClick={() => navigate("/edit-student/" + student._id)}
                      ></button>
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </div>
    </>
  );
}

export default StudentList;
