import { Field, Form, Formik } from "formik";
import React from "react";
import { useAuthContext } from "../context/AuthContext";
import { useStudentContext } from "../context/StudentContext";

function NewStudent() {
  let { saveNewStudent } = useStudentContext();
  let { userInfo } = useAuthContext();
  let initialValues = {
    name: "",
    course: "",
  };

  let onSubmit = (values, onSubmitProps) => {
    saveNewStudent(values);
    onSubmitProps.resetForm();
  };
  return (
    <>
      <div className="row justify-content-center">
        <div className="col-8 col-lg-4">
          <p className="text-center mt-4 h3 text-primary">Add New Student</p>
          <Formik initialValues={initialValues} onSubmit={onSubmit}>
            <Form className="card px-5 py-3">
              <div className="mb-3">
                <label htmlFor="" className="form-label">
                  Student Name
                </label>
                <Field
                  type="text"
                  name="name"
                  className="form-control"
                  autoComplete="off"
                />
              </div>
              <div className="mb-3">
                <label htmlFor="" className="form-label">
                  Course
                </label>
                <Field
                  as="select"
                  name="course"
                  type="text"
                  className="form-select"
                >
                  <option value="">---select course ---</option>
                  <option value="React">React</option>
                  <option value="Full Stack">Full Stack</option>
                  <option value="Front End">Front End</option>
                  <option value="Node JS">Node JS</option>
                </Field>
              </div>
              <button className="btn btn-success" type="submit">
                <span className="fa fa-save me-2"></span>
                Submit
              </button>
            </Form>
          </Formik>
        </div>
      </div>
    </>
  );
}

export default NewStudent;
