import { createContext, useState, useContext } from "react";
import { useNavigate } from "react-router-dom";

import { checkTokenForAccess } from "./../service/AuthService";
import {
  getList,
  deleteStud,
  postStud,
  getStudentDetails,
  updateStudent,
} from "./../service/StudentService";

const StudentContext = createContext({});

export function StudentContextProvider(props) {
  let { children } = props;
  let navigate = useNavigate();

  let [studentList, setStudentList] = useState([]);
  let [initRequest, setInitRequest] = useState(false);
  let [editInitValue, setEditInitValue] = useState({
    id: 0,
    name: "",
    course: "",
  });

  let getStudentList = () => {
    getList().then(function (data) {
      setStudentList([...data.studentList]);
      setInitRequest(true);
    });
  };

  let removeStudent = (id, index) => {
    checkTokenForAccess(navigate, () => {
      deleteStud(id).then((data) => {
        let { status, message } = data;
        if (status === true) {
          alert(message);
          let _studentList = [...studentList];
          _studentList.splice(index, 1);
          setStudentList(_studentList);
        } else {
          alert(message);
        }
      });
    });
  };

  let saveNewStudent = (values) => {
    checkTokenForAccess(navigate, () => {
      postStud(values).then((data) => {
        let { status, message } = data;
        if (status === true) {
          alert(message);
          getStudentList();
          navigate("/");
        } else {
          alert(message);
        }
      });
    });
  };

  let getSingleStudentDetails = (id) => {
    checkTokenForAccess(navigate, () => {
      getStudentDetails(id).then((data) => {
        let { status, message, details } = data;
        if (status === true) {
          setEditInitValue({ ...details });
        } else {
          alert(message);
        }
      });
    });
  };

  let updateStudentDetails = (values) => {
    checkTokenForAccess(navigate, () => {
      updateStudent(values).then((data) => {
        let { status, message } = data;
        if (status === true) {
          alert(message);
          getStudentList();
          navigate("/");
        } else {
          alert(message);
        }
      });
    });
  };

  let values = {
    studentList,
    getStudentList,
    removeStudent,
    saveNewStudent,
    initRequest,
    getSingleStudentDetails,
    editInitValue,
    updateStudentDetails,
  };

  return (
    <StudentContext.Provider value={values}>{children}</StudentContext.Provider>
  );
}

export function useStudentContext() {
  return useContext(StudentContext);
} // custom hooks
