import { Get, Post, Delete, BASE_URL, Patch } from "./index";

export const getList = async () => {
  try {
    let url = BASE_URL + "student/list";
    let result = await Get(url);
    return result.data;
  } catch (error) {
    console.log("error", error);
  }
};

export const deleteStud = async (id) => {
  try {
    let url = BASE_URL + "student/delete/" + id;
    let { data } = await Delete(url);
    return data;
  } catch (error) {
    console.log("error", error);
  }
};

export const postStud = async (values) => {
  try {
    let url = BASE_URL + "student/add-new";
    let { data } = await Post(url, values);
    return data;
  } catch (error) {
    console.log("error", error);
  }
};

export const getStudentDetails = async (id) => {
  try {
    let url = BASE_URL + "student/single-details/" + id;
    let { data } = await Get(url);
    return data;
  } catch (error) {
    console.log("error", error);
  }
};

export const updateStudent = async (values) => {
  try {
    let url = BASE_URL + "student/update";
    let { data } = await Patch(url, values);
    return data;
  } catch (error) {
    console.log("error", error);
  }
};
