import React, { useState, useLayoutEffect } from "react";
import { Link } from "react-router-dom";
import { useAuthContext } from "../context/AuthContext";

function NavBar() {
  let { userInfo, logout } = useAuthContext();
  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light">
      <div className="container-fluid">
        <Link className="navbar-brand text-primary" to="#">
          <span className="fa fa-gear"></span> Student Management
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav">
            <li className="nav-item">
              <Link className="nav-link active" aria-current="page" to="/">
                <span className="fa fa-list"></span> Student List
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/add-student">
                <span className="fa fa-user-plus"></span> New Student
              </Link>
            </li>
          </ul>
        </div>
        <div className="d-flex align-content-center">
          {userInfo === false ? null : (
            <>
              <span className="h5">Welcome, {userInfo.user}</span>
              <button
                className="fa fa-sign-out fa-2x  mx-2 btn btn-outline-danger btn-sm"
                onClick={logout}
              ></button>
            </>
          )}
        </div>
      </div>
    </nav>
  );
}
export default NavBar;
